'use client';

import { useState, useEffect } from 'react';
import Link from 'next/link';

interface Subcategory {
  id: number;
  name: string;
  image: string;
}

interface OccasionProductsSliderProps {
  subcategories: Subcategory[];
  mainCategory: string;
}

const OccasionProductsSlider = ({ subcategories, mainCategory }: OccasionProductsSliderProps) => {
  const [currentIndex, setCurrentIndex] = useState(0);

  const nextSlide = () => {
    setCurrentIndex((prev) => 
      prev >= subcategories.length - 1 ? 0 : prev + 1
    );
  };

  const prevSlide = () => {
    setCurrentIndex((prev) => 
      prev === 0 ? subcategories.length - 1 : prev - 1
    );
  };

  // Auto slide every 5 seconds
  useEffect(() => {
    if (subcategories.length <= 6) return; // Don't auto-slide if all items visible
    
    const timer = setInterval(() => {
      nextSlide();
    }, 5000);
    return () => clearInterval(timer);
  }, [currentIndex, subcategories.length]);

  // Get visible subcategories
  const getVisibleItems = () => {
    const itemsToShow = 6;
    if (subcategories.length <= itemsToShow) {
      return subcategories;
    }
    
    const visible = [];
    for (let i = 0; i < itemsToShow; i++) {
      const index = (currentIndex + i) % subcategories.length;
      visible.push(subcategories[index]);
    }
    return visible;
  };

  return (
    <section className="py-6 bg-[#F5F3ED]">
      <div className="w-full px-8 sm:px-12 lg:px-16 xl:px-24 2xl:px-32">
        <div className="relative max-w-4xl">
          <div className="overflow-hidden px-8">
            <div className="flex gap-3 justify-start">
              {getVisibleItems().map((subcat, idx) => (
                <Link 
                  key={`${subcat.id}-${idx}`} 
                  href={`/occasions/${mainCategory}/${subcat.id}`}
                  className="flex-shrink-0 group cursor-pointer"
                >
                  <div className="bg-white rounded-xl p-3 shadow-sm hover:shadow-md transition-all duration-300 border border-gray-100 w-28">
                    <div className="flex flex-col items-center gap-1.5">
                      <div className="relative w-10 h-10 flex-shrink-0">
                        <div className="w-full h-full bg-[#F5F1E8] rounded-full flex items-center justify-center overflow-hidden">
                          <span className="text-lg">{subcat.image}</span>
                        </div>
                      </div>
                      <h3 
                        className="text-xs font-medium text-gray-800 text-center line-clamp-2 leading-tight" 
                        style={{ fontFamily: 'var(--font-almarai)' }}
                      >
                        {subcat.name}
                      </h3>
                    </div>
                  </div>
                </Link>
              ))}
            </div>
          </div>

          {/* Navigation Arrows - Only show if more than 6 items */}
          {subcategories.length > 6 && (
            <>
              <button
                onClick={prevSlide}
                className="absolute left-0 top-1/2 -translate-y-1/2 bg-white hover:bg-gray-50 text-gray-800 p-2 rounded-full shadow-md transition-all duration-300 z-10"
                aria-label="Previous"
              >
                <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2.5} d="M15 19l-7-7 7-7" />
                </svg>
              </button>
              <button
                onClick={nextSlide}
                className="absolute right-0 top-1/2 -translate-y-1/2 bg-white hover:bg-gray-50 text-gray-800 p-2 rounded-full shadow-md transition-all duration-300 z-10"
                aria-label="Next"
              >
                <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2.5} d="M9 5l7 7-7 7" />
                </svg>
              </button>
            </>
          )}
        </div>
      </div>
    </section>
  );
};

export default OccasionProductsSlider;

